import { cssProps } from '@oracle/oraclejet-cssprops';
import { MessageVariant } from './Message.types';

const messageSummaryStyles = {
  // TODO: Reevaluate once the TEXT component is available (JET-46891)
  base: cssProps`${{
    flex: '1',
    overflowWrap: 'anywhere'
  }}`,

  banner: cssProps`${{
    fontSize: 'var(--oj-c-PRIVATE-DO-NOT-USE-message-banner-summary-text-size)',
    fontWeight: 'var(--oj-c-PRIVATE-DO-NOT-USE-message-banner-summary-text-weight)',
    color: 'var(--oj-c-PRIVATE-DO-NOT-USE-message-banner-summary-text-color)'
  }}`,

  inline: cssProps`${{
    fontSize: 'var(--oj-c-PRIVATE-DO-NOT-USE-user-assistance-inline-font-size)',
    fontWeight: '400',
    color: 'var(--oj-c-PRIVATE-DO-NOT-USE-user-assistance-inline-text-color)',
    lineHeight: 1.333
  }}`
};

/**
 * Props for the Summary component
 */
type Props = {
  /**
   * The summary text
   */
  text?: string;

  /**
   * Style variant
   */
  variant?: MessageVariant;
};

/**
 * Summary Component for rendering the summary text of the Message
 */
function MessageSummary({ text, variant = 'banner' }: Props) {
  const classes = `oj-c-message${variant}-summary ${messageSummaryStyles.base} ${messageSummaryStyles[variant]}`;
  return (
    <div role="heading" class={classes}>
      {text}
    </div>
  );
}

export { MessageSummary };
